  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
}
.new-female-profile--button {
  background-image: linear-gradient(to bottom, #cb83d2, #8b5a8f);
  border: solid 2px #d8d9d8;
  cursor: pointer;
  transition: all 0.3s ease;
}
.new-female-profile--button:hover {
  border-color: #999;
}
.new-female-profile-block-images {
  background-color: #d8d8d8;
  border-radius: 50%;
}
.new-female-profile-block-images img {
  border-radius: 50%;
}
.new-female-profile-rules-slider {
  position: relative;
}
.new-female-profile-rules-slider .owl-dots {
  padding-top: 18px;
}
.new-female-profile-rules-slider .owl-dots .owl-dot span {
  width: 5px !important;
  height: 5px !important;
}
.new-female-profile-rules-slider .owl-dots .active span {
  background-color: #ba78c0 !important;
}
.new-female-profile-rules-slider .owl-nav .owl-prev {
  position: absolute;
  left: 111px;
  top: 41px;
}
.new-female-profile-rules-slider .owl-nav .owl-prev:hover {
  background-color: transparent !important;
}
.new-female-profile-rules-slider .owl-nav .owl-prev i {
  font-size: 35px;
  color: #ba78c0;
}
.new-female-profile-rules-slider .owl-nav .owl-next {
  position: absolute;
  right: 111px;
  top: 41px;
}
.new-female-profile-rules-slider .owl-nav .owl-next:hover {
  background-color: transparent !important;
}
.new-female-profile-rules-slider .owl-nav .owl-next i {
  font-size: 35px;
  color: #ba78c0;
}
.new-female-profile-rules-slider--slide-items-item {
  overflow: hidden;
}
.new-female-profile-rules-slider--slide-items-item:first-child {
  margin-left: 0;
}

